@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(90deg, rgba(0, 170, 119, 1) 15%, rgba(21, 57, 220, 1) 50%, rgba(170, 30, 156, 1) 86%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);

  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg)
}

a {
  color: unset;
  text-decoration: none;
}

.gradient__bg

{

  /* ff 3.6+ */ 

  /* safari 5.1+,chrome 10+ */

  /* opera 11.10+ */
  background:-o-linear-gradient(343deg, rgba(75, 0, 123, 1) 41%, rgba(6, 6, 143, 1) 100%);

  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4B007B', endColorstr='#06068F', GradientType=1 );

  /* ie 10+ */

  /* global 94%+ browsers support */
  background:linear-gradient(343deg, rgba(75, 0, 123, 1) 41%, rgba(6, 6, 143, 1) 100%);

}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4rem 6rem;
}

.section__margin {
  margin: 4rem 6rem;
}

.scale-up-center {
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }

  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }
}


.blogs {
    display: flex;
    flex-direction: column;    
}

.blogs-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 5rem;
}

.blogs-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}

.blogs-container {
    display: flex;
    flex-direction: row;
}

.blogs-container_groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.blogs-container_groupB {
    flex: 1 1;

    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .blogs-container {
        flex-direction: column-reverse;
    }


    .blogs-container_groupA {
        margin: 2rem 0;
    }

    .blogs-container_groupA .blogs-container_article  {
       width: 48%;
    }

    .blogs-container_groupA .blogs-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .blogs-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .blogs-container_groupA .blogs-container_article  {
       width: 100%;
    }

    .blogs-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .blogs-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
.features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.features-container__feature-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.features-container_feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .features-container__feature {
        margin: 1rem 0;
    }
}
.navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0.5rem 2rem;
}

.navbar-links{
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.navbar-links_logo{
  margin-right: 2rem;
}

.navbar-links_logo img{
  width: 50px;
  height: 50px;
}

.navbar-links_logo p{
  align-items: center;
  color: white;
  font-weight: 500;
}

.navbar-links_container{
  display: flex;
  flex-direction: row;
}

.navbar-sign{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.navbar-links_container p,
.navbar-menu_container p,
.navbar-sign p{
  color: white;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}

.navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}

.navbar-menu svg{
  cursor: pointer;
}

.navbar-menu_container{
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

.navbar-menu_container p{
  margin: 1rem 0;
}

@media screen and (max-width: 1050px) {
  .navbar-links_container{
    display: none;
  }

  .navbar-menu {
    display: flex;
  }
}


@media screen and (max-width: 700px) {
    .navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .navbar {
        padding: 2rem;
    }

    .navbar-menu_container {
        top: 20px;
    }

    .navbar-menu_container-links-sign {
        display: block;
    }
}

.cyberSecurity {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.cyberSecurity-image img {
    width: 90%;
    height: 90%;
}

.cyberSecurity-heading {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.cyberSecurity-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    padding-bottom: 50px;
}

.cyberSecurity-heading p {
    color: var(--color-subtext);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);

    margin-top: 2rem;
}

.cyberSecurity-container {
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .cyberSecurity {
        flex-direction: column;
    }

    .cyberSecurity-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .cyberSecurity-heading h1 {
