}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .aboutus-feature .features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .aboutus-feature .features-container_feature-text {
        margin-top: 0.5rem;
    }

    .aboutus-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .aboutus-container .features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}
